<template>
  <swiper
    v-if="slides.length > 1 && !settings.enable_one_slide"
    id="HeroBanner"
    ref="heroSwiper"
    class="carousel swiper-container hero-banner hero-banner__carousel hero-banner__container"
    :auto-height="true"
    :modules="modules"
    :breakpoints="swiperSettings.breakpoints"
    :effect="'fade'"
    :fade-effect="{crossFade: false}"
    :autoplay="autoplaySettings"
    :loop="true"
    :enabled="!settings.enable_one_slide"
    :pagination="swiperSettings.pagination"
    :navigation="swiperSettings.navigation"
    @slideChange="updateParagraphBlock"
  >
    <div
      v-if="!settings.enable_one_slide"
      class="
        swiper-button-prev
        swiper-button-custom
      "
      :class="[
        `swiper-button-custom--${settings.banner_theme}`,
        { 'is-hidden':slides.length <= 1 }
      ]"
      js-hero-banner="prev"
    >
      <icon-chevron-left />
    </div>
    <swiper-slide
      v-for="(slide, index) in slides"
      :key="`slide-${index}`"
      :class="['hero-banner__slide',
               'lazyload']"
    >
      <template v-if="slide.video_url === '' || slide.video_url === '#'">
        <div
          class="hero-banner__image hero-banner__image--desktop lazyload"
          :data-bgset="`${slide.image} 1296w 585h`"
        />

        <div
          v-if="slide.mobile_image !== ''"
          class="hero-banner__image hero-banner__image--mobile lazyload"
          :data-bgset="`${slide.mobile_image} 1296w 585h`"
        />
        <div
          v-else
          class="hero-banner__image hero-banner__image--mobile lazyload"
          :data-bgset="`${slide.image} 1296w 585h`"
        />
      </template>
      <template v-else>
        <div
          class="hero-banner__player"
        >
          <video
            class="hero-banner__video"
            autoplay="1"
            loop="1"
            muted="1"
            playsinline="1"
            :poster="slide.image"
            preload="1"
          >
            <source
              :src="slide.video_url"
              type="video/mp4"
            >
          </video>
        </div>
      </template>

      <div
        class="
            hero-banner__paragraph
          "
        :class="[`hero-banner__paragraph--${slide.banner_theme}`, slide.alignment]"
        :data-alignment="slide.banner_theme"
      >
        <div class="hero-banner__content">
          <h2
            v-if="slide.heading !== ''"
            class="hero-banner__heading"
            v-text="slide.heading"
          />

          <p
            v-if="slide.desc !== ''"
            class="hero-banner__desc"
            v-text="slide.desc"
          />

          <div
            class="hero-banner__cta-group"
            :class="slide.alignment"
          >
            <a
              v-if="slide.primary_cta_link !== '' && slide.primary_cta_text !== ''"
              class="
                    text-link
                    hero-banner__cta
                    hero-banner__cta--primary
                  "
              :class="[`text-link--${slide.banner_theme } hero-banner__cta--${slide.banner_theme}`]"
              :aria-label="slide.primary_cta_aria.length ? slide.primary_cta_aria : slide.primary_cta_text"
              :href="slide.primary_cta_link"
            >
              <span v-text="slide.primary_cta_text" />
              <icon-arrow-up-right />
            </a>

            <a
              v-if="slide.secondary_cta_link !== '' && slide.secondary_cta_text !== ''"
              class="
                    text-link
                    hero-banner__cta
                    hero-banner__cta--secondary
                  "
              :class="[`text-link--${slide.banner_theme} hero-banner__cta--${slide.banner_theme }`]"
              :aria-label="slide.secondary_cta_aria.length ? slide.secondary_cta_aria : slide.secondary_cta_text "
              :href="slide.secondary_cta_link"
            >
              <span v-text="slide.secondary_cta_text" />
              <icon-arrow-up-right />
            </a>
          </div>
        </div>
      </div>
    </swiper-slide>
    <div
      v-if="!settings.enable_one_slide"
      class="
        swiper-button-next
        swiper-button-custom
      "
      :class="[
        `swiper-button-custom--${settings.banner_theme}`,
        { 'is-hidden': slides.length <= 1 }
      ]"
      js-hero-banner="next"
    >
      <icon-chevron-right />
    </div>

    <div
      v-if="!settings.enable_one_slide"
      ref="pagination"
      class="
        swiper-pagination
      "
      :class="[`swiper-pagination--${settings.banner_theme}`]"
      js-hero-banner="pagination"
    />
  </swiper>
</template>

<script>

/**
 * Vue: Hero banner (hero-banner)
 * -----------------------------------------------------------------------------
 * Hero Banner Block.
 *
 * @param {String} liquid.sectionId - Section's Liquid ID.
 *
 */

import breakpoints from '~/config/breakpoints'
import { Swiper, SwiperSlide } from 'swiper/vue'
import { Autoplay, EffectFade, Navigation, Pagination } from 'swiper'

import IconArrowUpRight from '~icons/direction/arrow/up-right.svg'
import IconChevronRight from '~icons/direction/chevron/right.svg'
import IconChevronLeft from '~icons/direction/chevron/left.svg'

export default {
  name: 'HeroBanner',

  components: {
    Swiper,
    SwiperSlide,
    IconArrowUpRight,
    IconChevronRight,
    IconChevronLeft,
  },

  props: {
    liquid: {
      type: Object,
      default: () => ({
        sectionId: '',
      }),
    },
    slides: {
      type: Array,
      default: () => ([]),
    },
    settings: {
      type: Object,
      default: () => ({}),
    },
  },

  setup() {
    return {
      modules: [
        Autoplay, EffectFade, Navigation, Pagination,
      ],
    }
  },

  data() {
    return {
      carouselClass: {
        'is-hidden': this.settings.enable_one_slide,
      },
      swiperSettings: {
        breakpoints: {
          [breakpoints.s]: {
            centeredSlides: false,
            slidesPerView: 1,
          },
          [breakpoints.m]: {
            loopedSlides: 1,
            slidesPerView: 1,
          },
          1380: {
            loopedSlides: 1,
            slidesPerView: 1,
          },
        },
        pagination: {
          el: '[js-hero-banner="pagination"]',
          type: 'bullets',
          clickable: true,
        },
        navigation: {
          nextEl: '[js-hero-banner="next"]',
          prevEl: '[js-hero-banner="prev"]',
        },
      },
    }
  },

  computed: {

    /**
     * Carousel autoplay settings.
     * @returns {String}
     */
    autoplaySettings() {
      const { carousel_autoplay, enable_one_slide } = this.settings
      if(!carousel_autoplay || enable_one_slide) {
        return
      }

      return { 'delay': `${Number(carousel_autoplay) * 1000}` }
    },

    swiper() {
      return this.$refs.heroSwiper.$el.swiper
    },
  },

  mounted() {
  },

  methods: {

    /**
     * Carousel pagingation settings
     * @returns {Object}
     */
    paginationSettings() {
      const paginationEl = this.$refs.pagination

      if(!paginationEl) {
        return
      }

      return { el: paginationEl, type: 'bullets', clickable: true }
    },

    /**
     * Slide Change Event Hander.
     */
    updateParagraphBlock() {
      const { realIndex } = this.swiper
      const visibleSlides = this.swiper.visibleSlides[0]

      if (!visibleSlides || this.settings.enable_one_slide) {
        return
      }

      const pagination = this.$refs.pagination

      /**
       * Reposition dot buttons by the height of paragraph.
       */
      let cell
      if (realIndex == null) {
        cell = document.querySelector('.swiper-slide-active .hero-banner__paragraph')
      } else {
        cell = document.querySelector(`[data-swiper-slide-index="${realIndex}"] .hero-banner__paragraph`)
      }

      if (!cell) {
        return
      }

      const cellBottom = (visibleSlides.offsetHeight / 2) - (cell.offsetHeight / 2)
      const paginationOffset = '4rem'
      pagination.style.bottom = `calc(${cellBottom}px + ${paginationOffset})`

      /**
       * Reposition dot buttons by the alignment value.
       */
      const alignment = cell.dataset.alignment
      if (!alignment) {
        return
      }

      pagination.classList.remove('right')
      pagination.classList.remove('left')
      pagination.classList.add(alignment)
    },

  },
}
</script>

<style lang="scss">
@import './hero-banner';
</style>
